@import '../_nmq-variables.less';
@import "../_nmq-mixins.less";


.fn-@{nmq-ns}-release-pic-list{
	background: #fff;
	padding-left: 3.2%;
	

	.pic-wrap{
		border-bottom: 1px solid @border-color-base;	
		padding-top: 10px;
		overflow: hidden;
	}

	.item{
		width: 20%;
		padding: 0 3.2% 10px 0;
		float: left;

		.item-wrap{
			position: relative;	
		}

		.close{
			.absolute(-10px,-7px,_,_);
			color: #666;
		}	
	}
}


@media (min-width: @screen-md-min) {
	.fn-@{nmq-ns}-release-pic-list{
		.item{
			.close{
				font-size: @font-size-xl;
			}
		}
	}
}