@import '../_nmq-variables.less';
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-release-text{
	@pd: 3.2;

	padding-left: unit(@pd, %);
	background: #fff;
	overflow: hidden;

	> * {
		outline: none;
		border: 0;
		border-bottom: 1px solid @border-color-base;
	}

	.title{
		width: 100%;
		padding: 10px unit(@pd, %) 10px 0;
		line-height:1.2;
	}

	.details{
		width: 100%;
		padding: 1% unit(@pd, %) 1% 0;
		min-height: 150px;
		line-height: 1.3;
		color: #666;
	}
}