@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";


.fn-@{nmq-ns}-choose-list{
	@pd: 1.3%;


	.size(100%);
	.absolute(_,_,0,0);
	padding-top:2.5rem;
	padding-bottom:3.4rem;

	.item-wrap{
		padding: 0 unit(@pd * 2, %) 0 unit(@pd, %);
		overflow-y: scroll;
		height:100%;

	}
	.item{
		display: block;
		width: 32%;
		float: left;
		margin: 0  0 unit(@pd, %) unit(@pd, %);
		position: relative;

		.choose-btn{
			.absolute(5%,5%,_,_);
			.size(15%);
			//.size(16px);

			input[type=checkbox]{
			    min-width: 0;
			    opacity: 0;
			    .size(100%);
			    outline: 0;
			    .absolute(0,_,_,0);
			}

			.icon{
				display: block;
			    .size(100%);
			    background: data-uri('@{nmq-image-path}/choose-list-btn.png') no-repeat top right;
			    background-size: 100% 100%;
			}

			input[type=checkbox]:checked + .icon{
				background: data-uri('@{nmq-image-path}/choose-list-btn-active.png') no-repeat top right;
				background-size: 100% 100%;
			}
		}

		.under-shelf{
			.absolute(_,_,0,0);
			width: 100%;
		    background: rgba(0, 0, 0, 0.6);
		    padding: 0 5px;
		    text-align: center;
		    color: #fff;
		    line-height: 1.8;
		    font-size: @font-size-base;
		    .ellipsis();
		}
	}
}
