@import '../_nmq-variables.less';
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-product-list{
	background: #fff;

	.list-wrap{
		padding: 3.2% 3.2% 0;
		overflow:hidden;
		position: relative;

		.item{
			background: @container-bg-color;
			padding: 10px;
			border-radius: 5px;
			position: relative;

			.img-box{
				border: 1px solid @border-color-base;
			}

			.info{
				padding-left: 10px;

				.tlt{
					.ellipsis(2);
					padding-bottom: 5px;
				}
			}

			.close{
				.absolute(-10px,-5px,_,_);
				color: #666;
			}
		}
	}
}


@media (min-width: @screen-md-min) {
	.fn-@{nmq-ns}-product-list{
		.list-wrap{
			.item{
				.close{
					font-size: @font-size-xl;
				}
			}	
		}
	}
}



