@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";


.fn-@{nmq-ns}-choose-btn{
	@color: #ff4e7a;

	background: #fff;
	padding:0.48rem 0.6rem;
	overflow: hidden;
	.absolute(0,_,_,0);
	z-index:10;

	.item{
		display: flex;
		padding:0.2rem;
		margin-right:0.4rem;
		text-align: center;
		border: 1px solid @color999;
		border-radius: 0.2rem;
		color: @color999;
		align-items: center;
		justify-content: center;
		float: left;

		&:before{
			.nmq-icon();
			content: "\e616";
			font-size: @font-size-lg;
			margin-right: 3px;
		}

		.text{
			font-size: @font-size-base;
		}

		&.active{
			color: @nmq-theme-color;
			border: 1px solid @color;

			&:before{
				color: @color;
			}
		}
	}
}



@media (min-width: @screen-md-min) {

	.fn-@{nmq-ns}-choose-btn{

		.item{
			&:before{
				font-size: @font-size-xxl;
			}

			.text{
				font-size: @font-size-lg;
			}
		}
	}
}
