@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";


.fn-@{nmq-ns}-choose-bottom{
	.fixed(_,_,0,_);
	width: 100%;
	max-width: @container-max-width;
	margin: 0 auto;	
	background: #fff;
	border-top: 1px solid @border-color-base;
	padding: 10px 0;

	.bottom-wrap{
		padding-right: 2%;
		overflow: hidden;
    	text-align: center;
    	display: flex;
    	justify-content: space-between;
    	align-items: center;

    	.choose-btn{
			padding-left: 20px;
			position: relative;
			display: flex;
			align-items: center;

			.btn{
				display: inline-block;
				.size(16px);
				margin: 0 5px 0 0;
				position: relative;
			}

			input[type=checkbox]{
			    min-width: 0;
			    opacity: 0;
			    .size(100%);
			    outline: 0;
			    .absolute(0,_,_,0);
			}

			.icon{
				display: block;
			    .size(100%);
			    background: data-uri('@{nmq-image-path}/choose-bottom-btn.png') no-repeat top right;
			    background-size: 100% 100%;
			}

			input[type=checkbox]:checked + .icon{
				background: data-uri('@{nmq-image-path}/choose-list-btn-active.png') no-repeat top right;
				background-size: 100% 100%;	
			}
    	}

		.fixed-right{
			.size(60px);
			background: @deep-color;
			color: #fff;
			border-radius: 60px;
			text-align: center;
			font-size: @font-size-base;
			padding-top: 10px;
			

			> *{
				.ellipsis();
				display: block;
			}
		}
	}
}


@media (min-width: @screen-md-min) {
	.@{container-class} {
		padding-bottom: 110px !important;
	}
	.fn-@{nmq-ns}-choose-bottom{
		
		.bottom-wrap{
			.choose-btn{
				font-size: @font-size-lg;
				.btn{
					.size(30px);
				}	
			}

			.fixed-right{
				.size(80px);
				border-radius: 80px;
				font-size: @font-size-lg;
				padding-top: 2.5%;	
			}
		}
	}
} 