@import '../_nmq-variables.less';
@import "../_nmq-mixins.less";



.fn-@{nmq-ns}-release-bottom{

	width: 100%;
	max-width: @container-max-width;
	.fixed(_,_,0,_);
	background: #fff;
	border-top: 1px solid @border-color-base;
	padding: 15px 0;
	margin: 0 auto;


	> a{
		display: block;
		float: left;
		margin-left: 10%;

	}

	.bottom-pic{
		.size(25px,20px);
		/*.size(41px,32px);*/
		background: data-uri('@{nmq-image-path}/release-bottom-pic01.png') no-repeat;
		background-size: 100% 100%;
	}

	.bottom-product{
		.size(23px);
		/*.size(40px,39px);*/
		background: data-uri('@{nmq-image-path}/release-bottom-pic02.png') no-repeat;
		background-size: 100% 100%;
		position: relative;

		em{
			.absolute(-32%,-64%,_,_);
		    color: #fff;
		    background: @deep-color;
		    font-size: .56rem;
		    .size(1.04rem,.8rem);
		    line-height: .8rem;
		    border-radius: 50%;
		    text-align: center;
		    .ellipsis();

		}
	}

	.bottom-release{
		float: right;
		text-align: right;
		padding-right: 3.2%;
		color: #999;

		.select{
			border: 0;
			color: @deep-color;
			font-size: .64rem;
			font-family: arial, 'Microsoft Yahei';
			appearance: none;
		}
	}


}
