@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";


.relevance-inner{
	.absolute(0,_,_,0);
	.size(100%);
	overflow-y: scroll;
	padding-bottom:2.1rem;
}

.fn-@{nmq-ns}-my-relevance{
	@pd: 5;

	.relevance-wrap{
		width: 93.6%;
		margin: 0 auto;
		overflow: hidden;

		.item{
			width: 49%;
			float: left;
			background: #fff;
			border-radius: 3px;
			padding: unit(@pd, px);
			margin: 1% 0;
			border: 1px solid #fff;
			position: relative;



			&:nth-child(even){
				 float: right;
			}

			.item-wrap{

				.img-box{

					.img{
						border-radius: 3px;
					}
				}

				.item-right{
					padding-left: 2%;

					>*{
						.ellipsis();
						font-size: @font-size-lg;
					}

					.num{
						color: #999;
						padding-top: 2%;
						font-size: @font-size-sm;

						&:before{
							.nmq-icon();
							content:"\e60e";
							font-size: @font-size-lg;
							color: #bbb;
						}
					}
				}
			}

			.checkbox{
				.absolute(_,0,0,_);
				.size(10%,27%);
				background: data-uri('@{nmq-image-path}/relevance-checkbox.png') no-repeat;
				background-size: 100% 100%;
				display: none;
			}

			&.active{
				border: 1px solid @nmq-theme-color;

				.checkbox{
					display: block;
				}
			}
		}
	}

	.info{
		width: 80%;
		background: #fff6e9;
		color: #ff9600;
		padding: unit(@pd, px) 0;
		margin: 0 auto 2%;
		text-align: center;
		border-radius: 25px;
		font-size: @font-size-sm;
		line-height: 1;
	}

	.no-relavance{
		background: #fff;
		height:100%;
		text-align: center;
		padding: 5% 0;

		.pic{
			display: block;
			width: 28.4%;
			margin:0 auto 5%;
			background: data-uri('@{nmq-image-path}/error-bg.png') no-repeat;
			background-size: 100% 100%;

			&:before{
				padding-top:92.5%;
			}
		}
		.txt{
			color: #666;
			font-size: @font-size-base;
		}
		.detail{
			color: #999;
			font-size: @font-size-base;
		}
	}
}
