@import "../_nmq-variables.less";
@import "../_nmq-mixins.less";

.fn-@{nmq-ns}-relevance-btn{
	width: 100%;
	max-width: @container-max-width;
	background: #fff;
	.fixed(_,_,0,_,);
	text-align: center;
	padding: 10px 0;

	.btn{
		display: inline-block;
		padding: 5px 25px;
		background: #aaa;
		border-radius: 4px;
		color: #fff;
		font-size: @font-size-lg;

		&.active{
			background: @deep-color;
		}

	}
}




